<template>
  <div class="model-detail">
    <div class="contentCellTitle">
      <div class="contentCellTitle-1"></div>基础信息
    </div>
    <div class="content">
      <div v-show="!form1EditFlg1">
        <div class="content-detail">
            <span>模型代码：{{modelData.modelCode}}</span>
            <span>模型名称：{{modelData.modelName}}</span>
            <span>模型类型：{{modelData.modelTypeStr}}</span>
            <span>模型算法：{{modelData.modelAlgorithmStr}}</span>
        </div>
      </div>
    </div>
    <div class="contentCellTitle">
      <div class="contentCellTitle-1"></div>算法参数
    </div>
    <div class="content">
      <div class="create-content" v-if="modelData.status!=3" @click="handleCancel(1)">
        <Icon type="ios-create-outline" />编辑
      </div>
      <div v-show="!form1EditFlg1">
        <div class="content-detail">
            <span>树的棵数：{{paramData.MaxIter}}</span>
            <span>树的最大深度：{{paramData.MaxDepth}}</span>
        </div>
      </div>
      <div v-show="form1EditFlg1">
        <Form
          ref="paramData"
          :model="paramData"
          :rules="ruleValidate1"
          :label-width="120"
          class="form-content"
        >
          <Col span="12">
            <FormItem label="树的棵数" prop="MaxIter">
              <Input-number v-model="paramData.MaxIter" :max="50" :min="5"></Input-number>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="树的最大深度" prop="MaxDepth">
              <Input-number v-model="paramData.MaxDepth" :max="20" :min="1"></Input-number>
            </FormItem>
          </Col>
          <Col span="12">
          <FormItem>
            <div class="button-content">
              <Button type="primary" @click="handleSubmit(1)">保存</Button>
              <Button @click="handleCancel(1)">取消</Button>
            </div>
          </FormItem>
          </Col>
        </Form>
      </div>
    </div>
    <div class="contentCellTitle">
      <div class="contentCellTitle-1"></div>预测数据
    </div>
    <div class="content">
      <div class="create-content" v-if="modelData.status!=3"  @click="handleCancel(2)">
        <Icon type="ios-create-outline" />编辑
      </div>
      <div v-show="!form1EditFlg2">
        <div class="content-detail">
            <span>样本集：{{modelData.sampleTable}}</span>
            <span>数据拆分比：{{modelData.splitRatio}}%</span>
            <span>特征列：{{modelData.featureColumn}}</span>
            <span>目标列：{{modelData.labelColumn}}</span>
            <span>预测集：{{modelData.forecastTable}}</span>
        </div>
      </div>
      <div v-show="form1EditFlg2">
        <Form
          ref="modelData"
          :model="modelData"
          :rules="ruleValidate2"
          :label-width="120"
          class="form-content"
        >
          <Col span="12">
            <FormItem label="样本集" prop="sampleTable">
              <Input v-model="modelData.sampleTable"></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="数据拆分比" prop="splitRatio">
              <Input-number v-model="modelData.splitRatio"></Input-number>
            </FormItem>
          </Col>
          <Col span="22">
            <FormItem label="特征列" prop="featureColumn">
              <Input v-model="modelData.featureColumn" style="width: 600px"></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="目标列" prop="labelColumn">
              <Input v-model="modelData.labelColumn"></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="预测集" prop="forecastTable">
              <Input v-model="modelData.forecastTable"></Input>
            </FormItem>
          </Col>
          <Col span="12">
          <FormItem>
            <div class="button-content">
              <Button type="primary" @click="handleSubmit(2)">保存</Button>
              <Button @click="handleCancel(2)">取消</Button>
            </div>
          </FormItem>
          </Col>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  props:['dataObj'],
  created(){},
  data() {
    return {
      modelData:{},
      paramData:{},
      form1EditFlg1:false,
      form1EditFlg2:false,
      ruleValidate1: {
        MaxIter: [
        ], 
        MaxDepth: [
        ],
      },
      ruleValidate2: {
        sampleTable: [
          {
            required: true,
            message: "必填！",
            trigger: "blur"
          }
        ],
        splitRatio: [],
        featureColumn: [
          {
            required: true,
            message: "必填！",
            trigger: "blur"
          }
        ],
        labelColumn: [
          {
            required: true,
            message: "必填！",
            trigger: "blur"
          }
        ],
        forecastTable: [
          {
            required: true,
            message: "必填！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.doLoadData();
  },
  watch: {
    dataObj(val){
      this.doLoadData();
    }
  },
  methods:{
    handleCancel(type){
        if(type == 1){
            this.form1EditFlg1 = !this.form1EditFlg1;
        }else if(type == 2){
            this.form1EditFlg2 = !this.form1EditFlg2;
        }
    },
    doLoadData(){
      this.axios
        .get("/modeling/get/"+this.dataObj.id)
        .then(response => {
            if (response.data) {
              this.modelData = response.data;
              this.paramData = JSON.parse(response.data.algorithmParam);
              this.form1EditFlg1 = false;
              this.form1EditFlg2 = false;
            } 
        })
        .catch(error => { console.log(error);}); 
    },
    handleSubmit(iNo){
      let name ="modelData";
      if(iNo==1){
        name ="paramData";
      }
      
      let valid = true;
      this.$refs[name].validate(vali => {
                      valid = vali;
                  });
      if (!valid) {
        return;
      }

      let data = {};
      if (iNo==2) {
         data = {
           id:this.modelData.id,
           sampleTable:this.modelData.sampleTable,
           splitRatio:this.modelData.splitRatio,
           featureColumn:this.modelData.featureColumn,
           labelColumn:this.modelData.labelColumn,
           forecastTable:this.modelData.forecastTable
         };
      } else {
        data = {
          id:this.modelData.id,
          algorithmParam:"{\"MaxIter\":"+this.paramData.MaxIter +",\"MaxDepth\":"+this.paramData.MaxDepth+"}"
        };
      }

      this.axios.post("/modeling/save/"+this.modelData.id, data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {              
              this.$Message.success('保存成功');
              this.handleCancel(iNo);
            })
            .catch(error => {});
    }
  }
};
</script>
<style lang="scss" scoped>
.model-detail {
  .content {
    position: relative;
    overflow: hidden;
    span {
      display: inline-block;
      font-size: 14px;
      margin: 10px 20px;
      min-width: 110px;
    }
    .content-detail {
      width: 90%;
    }
    .create-content {
      right: 0;
      position: absolute;
      cursor: pointer;
      font-size: 14px;
      color: #2d8cf0;
      i {
        font-size: 20px;
        color: #2d8cf0;
        vertical-align: -4px;
        padding-right: 2px;
      }
    }
  }
}
</style>


