<template>
  <div class="assess-report"> 
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellBody">
          <table align="center" style="text-align:center;font-weight:bold;width:400px;height:160px;">
            <caption>混淆矩阵与评估指标</caption>
            <tr>
              <td style="width:25%"></td>
              <td style="width:25%">模型预测(正)</td>
              <td style="width:25%">模型预测(负)</td>
              <td style="width:25%">总数</td></tr>
            <tr>
              <td>实际值(正)</td>
              <td style="background: #E0FFFF;">{{evaluator.TP}}</td>
              <td style="background: #AFEEEE;">{{evaluator.FN}}</td>
              <td style="background: #F0FFFF;">{{evaluator.NTS}}</td>
            </tr>
            <tr>
              <td>实际值(负)</td>
              <td style="background: #AFEEEE;">{{evaluator.FP}}</td>
              <td style="background: #E0FFFF;">{{evaluator.TN}}</td>
              <td style="background: #F0FFFF;">{{evaluator.NFS}}</td>
            </tr>
            <tr>
              <td>总数</td>
              <td style="background: #F0FFFF;">{{evaluator.PTS}}</td>
              <td style="background: #F0FFFF;">{{evaluator.PFS}}</td>
              <td>&nbsp;</td>
            </tr>            
          </table>
          <div ref="main" style="height:300px;"></div>
        </div>
      </div>
    </div>
   </div>
</template>
<script>
export default {
  components: {},
  props:['dataObj'],
  created() {},
  data() {
    return {
      evaluator:{},
      chartXAxisData:['召回率(Recall)','TPR(Sensitivity)','精准率(Precision)','NPV','准确率(Accuracy)','F1-Score'],
      chartSeriesData:[],
    };
  },
  mounted() {
    this.axios
        .get("/modeling/assess/"+this.dataObj.id)
        .then(response => {
            if (response.data) {
              this.evaluator =  JSON.parse(response.data.evaluator);
              this.chartSeriesData =[this.evaluator.recall,this.evaluator.sensitivity,this.evaluator.precision,this.evaluator.NPV,this.evaluator.accuracy,this.evaluator.f1Score];
              this.drawLine();
            }
        })
        .catch(error => {});
  },
  watch: {
    dataObj(val){
      this.axios
            .get("/modeling/assess/"+val.id)
            .then(response => {
                if (response.data) {
                  this.evaluator =  JSON.parse(response.data.evaluator);
                  this.chartSeriesData =[this.evaluator.recall,this.evaluator.sensitivity,this.evaluator.precision,this.evaluator.NPV,this.evaluator.accuracy,this.evaluator.f1Score];
                  this.drawLine();
                }
            })
            .catch(error => {});
    }
  },
  methods:{
    drawLine(){   
        let myChart = this.$echarts.init(this.$refs.main);
        myChart.setOption({
            color: ['#3398DB'],
            xAxis: {
                data: this.chartXAxisData,
                axisLabel: {interval:0,rotate: 30}
            },
            yAxis: {min:0,max:1,splitNumber:5},
            series: [{
                type: 'bar',
                barWidth:40,
                label:{normal:{show:true,position:'top'}},
                data: this.chartSeriesData
            }]
        });
    }
  }
};
</script>
<style lang="scss" scoped>
    .assess-report{
        font-size: 14px;
    }
</style>



