<template>
  <div class="param-dialog">
    <div class="contentCellBody">
      <Row>
        <table class="tableBox">
          <thead>
            <tr>
              <td v-for="(item,index) in tableHeader">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <template v-for="(key,value,index) in tableBody.algorithmParamJason">
              <tr>
                <td>{{index+1}}</td>
                <td>{{value}}</td>
                <td>{{key}}</td>
             </tr>
            </template>
          </tbody>
        </table>
      </Row>
    </div>
  </div>
</template>
<script>
export default {
    props:['dataObj'],
    data(){
        return{
            tableHeader: [
                {name:"序"}, 
                {name:"参数名"},
                {name:"参数值"},
            ],
            tableBody: {}
        }
    },
   mounted() {
     this.axios
        .get("/modeling/modelRecord/"+this.dataObj.id)
        .then(response => {
            if (response.data) {
              this.tableBody = response.data;
           } 
        })
        .catch(error => { console.log(error);}); 
  },
  watch: {
    dataObj(val){
     this.axios
        .get("/modeling/modelRecord/"+this.dataObj.id)
        .then(response => {
            if (response.data) {
              this.tableBody = response.data;
           } 
        })
        .catch(error => { console.log(error);}); 
    }
  }
};
</script>


