<template>
    <div>
    <Tabs type="card">
        <TabPane label="模型详情">
            <modelDetail :dataObj="dataObj"></modelDetail>
        </TabPane>
        <TabPane label="二分类评估报告">
            <assessReport :dataObj="dataObj"></assessReport>
        </TabPane>
    </Tabs>
    </div>
</template>
<script>
import modelDetail from './modelDetail'
import assessReport from './assessReport'
export default {
    components:{
        modelDetail,
        assessReport
    },
    props:['dataObj']
}
</script>


