<template>
  <div> 
    <div class="topTitle">
      <span class="topTitle-span1">机器学习</span>
      <span class="topTitle-span1">
        <img src="../../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机器学习建模</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>机器学习建模
          <div class="button-content">            
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>{{item.modelCode}}</td>
                    <td>{{item.modelName}}</td>                  
                    <td>{{item.modelTypeStr}}</td>
                    <td>{{item.modelAlgorithm}}</td>
                    <td>{{item.statusStr}}</td>
                    <td>{{item.createTime}}</td>
                    <td>{{item.experiBegTime}}~{{item.experiEndTime}}</td>
                    <td>{{item.deployTime}}</td>
                    <td>
                      <span class="spanBox" @click="showModel(item.id,0)">模型详情</span>
                      <span class="spanBox" v-if="item.status!=3" @click="showModel(item.id,1)">训练模型</span>
                      <span class="spanBox" v-if="item.status==2" @click="showModel(item.id,2)">部署模型</span>
                      <span class="spanBox" v-if="item.status==3" @click="showModel(item.id,3)">下架模型</span>
                      <span class="spanBox" v-if="item.status!=0" @click="showModel(item.id,5)">训练记录</span>
                    </td>
                   </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"   v-model="showModal" :title="modelTitle" @on-cancel="cancel" class="dialog" :styles="{width: '60%'}" >
      <component :is="tabview" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import modelDialog from "./modelDialog";
import vertifyRecords from "./vertifyRecords";
export default {
  components: {
    modelDialog,
    vertifyRecords
  },
  created() {
    this.loadData();
  },
  data() {
    return {
      tabview:'',
      showModal: false,
      dataObj:{
      },
      modelTitle: "",        
      tableHeader: [
        { name: "序" },
        { name: "模型代码" },
        { name: "模型名称" },
        { name: "模型类型" },
        { name: "模型算法" },
        { name: "状态" },
        { name: "创建时间" },
        { name: "实验时间" },
        { name: "上线时间" },
        { name: "操作" },
      ],
      tableBody: [{}]
    };
  },
  mounted() {},
  methods:{
    cancel() {},
    showModel(id, type){
      if(type == '0'){
        this.modelTitle = '模型详情'
        this.tabview = 'modelDialog';
        this.showModal = true;
        this.dataObj = {id:id};
      } else if(type == '1'){
        this.$Modal.confirm({
              title: '确认对话框',
              content: '<p>请确认，训练模型？</p>',
              onOk: () => {
                  this.doTraining(id);    
              },
              onCancel: () => {}
        });
      } else if(type == '2'){
        this.$Modal.confirm({
              title: '确认对话框',
              content: '<p>请确认，部署模型？</p>',
              onOk: () => {
                  this.doPublish(id);    
              },
              onCancel: () => {}
        });      
      } else if(type == '3'){
        this.$Modal.confirm({
              title: '确认对话框',
              content: '<p>请确认，下架模型？</p>',
              onOk: () => {
                  this.doOffline(id);    
              },
              onCancel: () => {}
        });      
      } else if(type == '5'){
        this.modelTitle = '训练记录'
        this.tabview = 'vertifyRecords';
        this.showModal = true;
        this.dataObj = {id:id};
      }
    },
    saveDatas(){},
    cancelDialog(){},
    loadData() {
      this.axios.get("/modeling/list")
            .then(response => {
                if (response.data) {
                  this.tableBody = response.data;
                }
            })
            .catch(error => {});
    },
    doTraining(id) {
      this.axios.get("/modeling/training/"+id)
            .then(response => {
              this.$Message.success('成功提交后台处理模型训练！');
              this.loadData();
            })
            .catch(error => {
              this.$Message.error('提交模型训练失败！');
            });
    },
    doPublish(id) {
      this.axios.get("/modeling/publish/"+id)
            .then(response => {
                this.$Message.success('部署模型成功！');
                this.loadData();
            })
            .catch(error => {
              this.$Message.error('部署模型失败！');
            });
    },
    doOffline(id) {
      this.axios.get("/modeling/offline/"+id)
            .then(response => {
                this.$Message.success('下架模型成功！');
                this.loadData();             
            })
            .catch(error => {
              this.$Message.error('下架模型失败！');
            });
    }
  }
};
</script>



</style>


