<template>
  <div class="vertify-records">
    <div class="contentCellBody">
      <Row>
        <table class="tableBox">
          <thead>
            <tr>
              <td v-for="(item,index) in tableHeader">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item,index) in tableBody">
              <tr>
                <td>{{index+1}}</td>
                <td>{{item.modelCode}}</td>
                <td>{{item.modelName}}</td>
                <td>{{item.evaluatorJson.AUC}}</td>
                <td>{{item.evaluatorJson.precision}}</td>
                <td>{{item.evaluatorJson.recall}}</td>
                <td>{{item.begTime}}</td>
                <td>{{item.endTime}}</td>
                <td>{{item.statusStr}}</td>
                <td>
                  <span class="spanBox" @click="operate(item.id)">参数</span>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </Row>
    </div>
    <Modal :mask-closable="false"   v-model="showModal" title="参数" @on-cancel="cancel" class="dialog" :styles="{width: '40%'}" >
      <paramDialog  :dataObj="dataObj2" ></paramDialog>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import paramDialog from './paramDialog'
export default {
  components:{
    paramDialog
  },
  props:['dataObj'],
  data(){
      return{
          dataObj:{},
          dataObj2:{id:1},
          showModal:false,
          tableHeader: [
              {name:"序"},
              {name:"模型代码"},
              {name:"模型名称"},
              {name:"AUC"},
              {name:"精准率"},
              {name:"召回率"},
              {name:"执行开始时间	"},
              {name:"执行结束时间"},
              {name:"运行状态"},
              {name:"操作"}
          ],
          tableBody: []
      }
  },
  mounted() {
     this.axios
        .get("/modeling/modelRecords?id="+this.dataObj.id)
        .then(response => {
            if (response.data) {
              this.tableBody = response.data;
           } 
        })
        .catch(error => { console.log(error);}); 
  },
  watch: {
    dataObj(val){
     this.axios
        .get("/modeling/modelRecords?id="+this.dataObj.id)
        .then(response => {
            if (response.data) {
              this.tableBody = response.data;
           } 
        })
        .catch(error => { console.log(error);}); 
    }
  },
  methods:{
      operate(id){
          this.showModal = true;
          this.dataObj2 = {id:id};
      },
      cancel(){}
  }
};
</script>


